.navbar {
    position:flex;
    background-color:rgb(165, 227, 243);
    padding: 10px;
    height: 50px; 
    width: 100%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

ul {
    list-style: none;
}

li {
    
    float: left; 
    margin-right: 40px;
}


li h4 {
    margin: 0;
}

a {
    text-decoration: none;
    color: rgb(0, 0, 0);
}